import React, { useContext, useState, useMemo } from 'react'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import Slider from 'react-slick'
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import useBuyCyblocsPacks from 'hooks/useBuyCyblocsPacks'
import useMediaQuery from 'hooks/useMediaQuery'
import contractConfig from 'config/constant/contract'
import { packageConfig } from 'config/constant'
import { PurchaseContext } from './context/PurchaseContext'
import { setSelectedPackage, changeNumberOfPackage, setLoading } from './store/reducer'
import PackageCommon from './components/PackageCommon'
import PackageRare from './components/PackageRare'
import PackageEpic from './components/PackageEpic'

const Wrapper = styled(Flex)`
  margin: 0 auto;
  position: relative;
  max-width: 1000px;
  padding: 45px 0px;

  @media (max-width: 768px) {
    padding-bottom: 0px;
  }
`
const SliderWrapper = styled(Box)`
  max-width: 100%;
  position: relative;
  z-index: 10;
  margin: 0 auto;
  padding: 0 100px;

  @media (max-width: 576px) {
    padding: 0px;
  }
`
const BackIcon = styled.img`
  width: 90px;
  margin-right: 20px;
  opacity: 0.6;
  height: 50px;
  opacity: ${(props) => (props.disabled ? '0.2' : '0.6')};
  transition: opacity 425ms ease;
  left: -100px;
  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
    opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  }

  @media (max-width: 768px) {
    width: 60px;
    height: 40px;
    left: -60px;
    top: 25%;
  }

  @media (max-width: 576px) {
    height: 40px;
    width: 60px;
    left: 0px;
    top: 25%;
    z-index: 999;
  }
`
const BackgroundImage = styled(Box)`
  width: 1000px;
  position: absolute;
  left: 0%;
  top: -20%;
  height: 300px;
  margin-top: 141px;
  z-index: 1;
  background-image: radial-gradient(circle farthest-corner at 50% 50%, rgba(78, 50, 124, 0.43), #1b1522 74%);
  opacity: 0.3;
`

const ForwardIcon = styled.img`
  width: 90px;
  margin-left: 20px;
  height: 50px;
  right: -100px;
  opacity: ${(props) => (props.disabled ? '0.2' : '0.6')};
  transition: opacity 425ms ease;
  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
    opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  }

  @media (max-width: 780px) {
    width: 60px;
    height: 40px;
    right: -60px;
    top: 25%;
  }

  @media (max-width: 576px) {
    height: 40px;
    right: 0px;
    width: 60px;
    z-index: 999;
  }
`
const StyledText = styled(Text)`
  width: 33%;
  text-align: center;
  font-size: 3rem;
  cursor: pointer;
  @media (max-width: 576px) {
    font-size: 2rem;
    font-weight: 700;
  }
`
const UnionImage = styled.img`
  width: 350px;
  bottom: -10px;
  position: absolute;

  @media (max-width: 780px) {
    width: 200px;
    bottom: 100px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const UnionImage2 = styled.img`
  position: absolute;
  width: 350px;
  bottom: 90px;
  left: 375px;

  @media (max-width: 780px) {
    width: 200px;
    bottom: 200px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const UnionImage3 = styled.img`
  width: 350px;
  bottom: -10px;
  position: absolute;
  right: 0px;

  @media (max-width: 780px) {
    bottom: 100px;
    width: 200px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const NextArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_right.png',
    1: '/images/arrow_rare_right.png',
    2: '/images/arrow_epic_right.png',
  }

  return (
    <ForwardIcon
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      disabled={disabled}
      src={IMAGE_MAP[index]}
      style={{
        ...style,
      }}
    />
  )
}

const BackArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_left.png',
    1: '/images/arrow_rare_left.png',
    2: '/images/arrow_epic_left.png',
  }
  return (
    <BackIcon
      disabled={disabled}
      src={IMAGE_MAP[index]}
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      style={{
        ...style,
      }}
    />
  )
}

const Package = () => {
  const { state, purchaseDispatch } = useContext(PurchaseContext)
  const { account } = useWeb3React()
  const [index, setIndex] = useState(0)
  const [nav1, setNav1] = useState()
  const history = useHistory()
  const [nav2, setNav2] = useState()
  const { selectedPackage, numberOfPackage, isLoading } = state
  const isMobile = useMediaQuery('(max-width: 576px)')
  const {
    cyblockPresale: { commonPackageContractAddress, rarePackageContractAddress, epicPackageContractAddress },
  } = contractConfig
  const { packagePrice } = packageConfig
  const { onBuyPack: onBuyCommonPackage } = useBuyCyblocsPacks(commonPackageContractAddress)
  const { onBuyPack: onBuyRarePackage } = useBuyCyblocsPacks(rarePackageContractAddress)
  const { onBuyPack: onBuyEpicPackage } = useBuyCyblocsPacks(epicPackageContractAddress)

  const payAmount = useMemo(() => {
    return new BigNumber(numberOfPackage).multipliedBy(new BigNumber(packagePrice)).toString()
  }, [numberOfPackage, packagePrice])
  // Setting for the slider
  const settings = useMemo(() => {
    return {
      dots: false,
      infinite: true,
      fade: true,
      draggable: false,
      swipe: false,
      speed: 500,
      cssEase: 'cubic-bezier(0.165, 0.840, 0.440, 1.000)',
      slidesToShow: 1,
      slidesToScroll: 1,
      nextArrow: <NextArrow index={index} disabled={isLoading} />,
      prevArrow: <BackArrow index={index} disabled={isLoading} />,
      beforeChange: (current, next) => {
        setIndex(next)
        purchaseDispatch(changeNumberOfPackage(1))
        if (next === 0) {
          purchaseDispatch(setSelectedPackage('common'))
        }

        if (next === 1) {
          purchaseDispatch(setSelectedPackage('rare'))
        }

        if (next === 2) {
          purchaseDispatch(setSelectedPackage('epic'))
        }
      },
    }
  }, [index, isLoading])

  // Function to change number of package which user want to buy
  const onChangeValue = (status) => {
    if (status === 'up') {
      purchaseDispatch(changeNumberOfPackage(numberOfPackage + 1))
    }

    if (status === 'down' && numberOfPackage > 1) {
      purchaseDispatch(changeNumberOfPackage(numberOfPackage - 1))
    }
  }

  const onHandleChangeSlide = (i) => {
    if (nav2) {
      nav2.slickGoTo(i)
    }
  }

  const onHandleBuyPack = async () => {
    try {
      let txStatus = null
      if (!account) {
        toast.error('Log in before purchasing', {
          hideProgressBar: true,
        })
        return
      }

      purchaseDispatch(setLoading(true))
      if (selectedPackage === 'common') {
        const { status } = await onBuyCommonPackage(String(numberOfPackage), payAmount)
        txStatus = status
      } else if (selectedPackage === 'rare') {
        const { status } = await onBuyRarePackage(String(numberOfPackage), payAmount)
        txStatus = status
      } else {
        const { status } = await onBuyEpicPackage(String(numberOfPackage), payAmount)
        txStatus = status
      }
      if (txStatus) {
        purchaseDispatch(setLoading(false))
        toast.success('Purchase successfully', {
          hideProgressBar: true,
        })
        history.push('/stash')
      }
    } catch (error) {
      console.log(error, 'error')
      toast.error('Fail to buy package', {
        hideProgressBar: true,
      })
      purchaseDispatch(setLoading(false))
    }
  }
  if (isMobile) {
    return (
      <Wrapper flexDirection="column" justifyContent="space-between">
        <Flex width="100%">
          <SliderWrapper>
            <Slider
              asNavFor={nav1}
              ref={(slider2) => setNav2(slider2)}
              slidesToShow={1}
              slidesToScroll={1}
              infinite
              fade
              draggable={false}
              swipe={false}
              speed={500}
              dots={false}
              arrows={false}
            >
              <Box>
                <Flex pb="30px">
                  <StyledText color="#D3D3D3" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
              <Box>
                <Flex pb="30px">
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#F2C552" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
              <Box>
                <Flex pb="30px">
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#9163FF" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
            </Slider>
          </SliderWrapper>
        </Flex>
        <SliderWrapper>
          <Slider asNavFor={nav2} ref={(slider1) => setNav1(slider1)} {...settings}>
            <PackageCommon onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
            <PackageRare onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
            <PackageEpic onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          </Slider>
        </SliderWrapper>
      </Wrapper>
    )
  }

  return (
    <Wrapper flexDirection="column" justifyContent="space-between">
      <SliderWrapper>
        <Slider asNavFor={nav2} ref={(slider1) => setNav1(slider1)} {...settings}>
          <PackageCommon onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          <PackageRare onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          <PackageEpic onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
        </Slider>
      </SliderWrapper>
      <UnionImage src="/images/Union.svg" />
      <UnionImage2 src="/images/Union.svg" />
      <UnionImage3 src="/images/Union.svg" />
      <Flex width="100%">
        <SliderWrapper>
          <Slider
            asNavFor={nav1}
            ref={(slider2) => setNav2(slider2)}
            slidesToShow={1}
            slidesToScroll={1}
            infinite
            fade
            draggable={false}
            swipe={false}
            speed={500}
            dots={false}
            arrows={false}
          >
            <Box>
              <Flex pt="60px" pb="60px">
                <StyledText color="#D3D3D3" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
            <Box>
              <Flex pt="60px" pb="60px">
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#F2C552" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
            <Box>
              <Flex pt="60px" pb="60px">
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#9163FF" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
          </Slider>
        </SliderWrapper>
      </Flex>
    </Wrapper>
  )
}

export default Package
