import React from 'react'
import styled from 'styled-components'

const StyledText = styled.a`
  font-size: 1.7rem;
  color: #e0e0e0;
  font-weight: 700;
  cursor: pointer;
  opacity: ${(props) => (props.isActive ? '1' : '0.7')};
  padding: 20px;
  text-decoration: none;
  transition: opacity 400ms ease;

  &:hover {
    opacity: 1;
  }
`

const MenuItem = ({ isActive, name, onNavigateTo, path }) => (
  <StyledText isActive={isActive} onClick={() => onNavigateTo(path)}>
    {name}
  </StyledText>
)

export default MenuItem
