import React from 'react'
import styled from 'styled-components'
import { ConnectorNames, connectorLocalStorageKey } from 'utils/web3React'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import useAuth from 'hooks/useAuth'
import Modal from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  @media (max-width: 576px) {
    padding: 75px 0px;
  }
`

const WalletButton = styled(Flex)`
  justify-content: center;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const StyledModalHeader = styled(Text)`
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
  padding: 0 60px;
  color: #ffffff;

  @media (max-width: 576px) {
    padding: 0 30px;
  }
`

const StyledSubText = styled(Text)`
  font-size: 1rem;
  color: #ffffff;
  line-height: 20px;
  letter-spacing: 2px;
`

const connectors = [
  {
    title: 'Metamask',
    connectorId: ConnectorNames.Injected,
  },
  {
    title: 'WalletConnect',
    connectorId: ConnectorNames.WalletConnect,
  },
  {
    title: 'Binance Wallet',
    connectorId: ConnectorNames.BSC,
  },
]

const LoginModal = ({ onDismiss }) => {
  const { login } = useAuth()
  return (
    <Modal title="Your wallet">
      <Wrapper>
        <Box>
          <StyledModalHeader>Log in by connecting a wallet</StyledModalHeader>
        </Box>
        <Box mb="15px">
          <WalletButton
            onClick={() => {
              login(connectors[0].connectorId)
              localStorage.setItem(connectorLocalStorageKey, connectors[0].connectorId)
              onDismiss()
            }}
          >
            <Text mr="5px">Metamask</Text>
          </WalletButton>
          <WalletButton
            onClick={() => {
              login(connectors[1].connectorId)
              localStorage.setItem(connectorLocalStorageKey, connectors[1].connectorId)
              onDismiss()
            }}
          >
            <Text mr="5px">WalletConnect</Text>
          </WalletButton>
          <WalletButton
            onClick={() => {
              login(connectors[2].connectorId)
              localStorage.setItem(connectorLocalStorageKey, connectors[2].connectorId)
              onDismiss()
            }}
          >
            <Text mr="5px">Binance Wallet</Text>
          </WalletButton>
          {/* <WalletButton>
            <Text mr="5px">Fortmatic</Text>
          </WalletButton>
          <WalletButton>
            <Text mr="5px">Portis</Text>
          </WalletButton> */}
        </Box>

        <StyledSubText>
          By connecting a wallet, you agree to Uniswap Lab Terms of Service and acknowledge that you have read and
          understand the Cyball disclaimer
        </StyledSubText>
      </Wrapper>
    </Modal>
  )
}

export default LoginModal
