import React from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { useModal, LoginModal, LogOutModal } from 'components/Modal'
import Box from 'components/Box'
import Text from 'components/Text'

const StyledLoginButton = styled.button`
  cursor: pointer;
  border: 0;
  font-size: 1.7rem;
  color: #e0e0e0;
  font-weight: 700;
  opacity: 0.7;
  color: #b89ef4;
  cursor: pointer;
  padding: 20px;
  background-color: transparent;
  background-image: linear-gradient(
    180deg,
    rgba(28, 22, 34, 0.98) 46%,
    rgba(31, 25, 36, 0.54) 84%,
    rgba(35, 30, 39, 0)
  );
  text-decoration: none;
  transition: opacity 400ms ease;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }
`
const AccountWrapper = styled(Box)`
  position: relative;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
`

const AccountText = styled(Text)`
  position: absolute;
  font-weight: 500;
  font-size: 1.7rem;
  line-height: 22.5px;
  color: #e0e0e0;
  letter-spacing: 2px;
`

const FrameImage = styled.img`
  width: 180px;
`

const UserBlock = () => {
  const [onPresentLoginModal] = useModal(<LoginModal />)
  const [onPresentLogOutModal] = useModal(<LogOutModal />)
  const { account } = useWeb3React()

  const formatAddress = (address) => {
    if (address) {
      const addressArr = address.split('')
      return `${addressArr.slice(0, 4).join('')}...${addressArr.slice(-4).join('')}`
    }

    return null
  }

  if (account) {
    return (
      <AccountWrapper onClick={onPresentLogOutModal}>
        <FrameImage src="/images/account-frame.png" />
        <AccountText>{formatAddress(account)}</AccountText>
      </AccountWrapper>
    )
  }

  return <StyledLoginButton onClick={onPresentLoginModal}>CONNECT WALLET</StyledLoginButton>
}

export default UserBlock
