import React from 'react'
import styled from 'styled-components'
import { toast } from 'react-toastify'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import useOpenPackage from 'hooks/useOpenPackage'
import contractConfig from 'config/constant/contract'
import { SubModal } from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  padding: 20px 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
`
const StyledText = styled(Text)`
  font-size: 2rem;
  color: #fefefe;
  line-height: 17.15px;
  letter-spacing: 2px;
  margin-bottom: 15px;
  font-weight: 700;
`

const Button = styled(Flex)`
  justify-content: center;
  align-items: center;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 200px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-right: 10px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const OpenSinglePackModal = ({ onDismiss, data }) => {
  const {
    cyblockPresale: { commonPackageContractAddress, rarePackageContractAddress, epicPackageContractAddress },
  } = contractConfig
  const { index, packageType } = data
  const { onUnpack: onUnPackCommonPackage } = useOpenPackage(commonPackageContractAddress)
  const { onUnpack: onUnpackRarePackage } = useOpenPackage(rarePackageContractAddress)
  const { onUnpack: onUnpackEpicPackage } = useOpenPackage(epicPackageContractAddress)

  const onHandleOpenPackage = async () => {
    try {
      const packageData = [
        [
          Number(index),
          [
            [1, 2, 2, 1, '120398102938019238098'],
            [1, 0, 1, 1, '838293481723'],
            [1, 1, 0, 1, '584930802374'],
          ],
        ],
      ]

      const proofs = packageData.map(() => {
        return [
          1,
          '0x63616e6469646174653100000000000000000000000000000000000000000000',
          '0x63616e6469646174653100000000000000000000000000000000000000000000',
        ]
      })
      const cyblocsData = JSON.parse(localStorage.getItem('cyblocs'))
      let result = null
      if (packageType === 'common') {
        result = await onUnPackCommonPackage(packageData, proofs)
        localStorage.setItem(
          'cyblocs',
          JSON.stringify({
            ...cyblocsData,
            common: cyblocsData.common + 1,
          }),
        )
      } else if (packageType === 'rare') {
        result = await onUnpackRarePackage(packageData, proofs)
        localStorage.setItem(
          'cyblocs',
          JSON.stringify({
            ...cyblocsData,
            rare: cyblocsData.rare + 1,
          }),
        )
      } else {
        result = await onUnpackEpicPackage(packageData, proofs)
        localStorage.setItem(
          'cyblocs',
          JSON.stringify({
            ...cyblocsData,
            epic: cyblocsData.epic + 1,
          }),
        )
      }
      onDismiss()
    } catch (error) {
      console.log(error, 'error ?')
      toast.error('Fail to open package', {
        hideProgressBar: true,
      })
    }
  }

  return (
    <SubModal title="Your wallet">
      <Wrapper>
        <StyledText>Open pack {index}</StyledText>
        <Flex alignItems="center">
          <Button onClick={onHandleOpenPackage}>
            <Text>Confirm</Text>
          </Button>
          <Button onClick={onDismiss}>
            <Text>Decline</Text>
          </Button>
        </Flex>
      </Wrapper>
    </SubModal>
  )
}

export default OpenSinglePackModal
