import React from 'react'
import styled from 'styled-components'
import Heading from 'components/Heading'
import Flex from 'components/Flex'
import Box from 'components/Box'

export const ModalHeader = styled.div`
  align-items: center;
  background: red;
  border-bottom: 1px solid black;
  display: flex;
  padding: 12px 24px;
`

export const ModalTitle = styled(Flex)`
  align-items: center;
  flex: 1;
`

export const ModalBody = styled(Flex)`
  flex-direction: column;
  height: 100%;
  padding: 24px 24px 12px 24px;
`

export const ModalCloseButton = ({ onDismiss }) => <button onClick={onDismiss}>close</button>

export const ModalBackButton = ({ onBack }) => <button onClick={onBack}>Back</button>

export const ModalContainer = styled(Box)`
  margin: 0 auto;
  z-index: 999;
  background-image: url('/images/login-modal.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 5px 70px;
  height: ${(props) => (props.height ? props.height : '500px')};
  width: 650px;

  @media (max-width: 576px) {
    width: 300px;
    height: 500px;
    padding: 0px;
  }
`

const SubModalContainer = styled(Box)`
  margin: 0 auto;
  z-index: 999;
  background-image: url('/images/logout-modal-frame.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 60px 40px 40px 40px;
  width: 450px;
`

const Modal = ({
  title,
  onDismiss,
  onBack,
  children,
  showModalHeader = false,
  hideCloseButton = false,
  headerBackground = 'transparent',
  minWidth = '320px',
  ...props
}) => (
  <ModalContainer minWidth={minWidth} {...props}>
    {showModalHeader && (
      <ModalHeader background={headerBackground}>
        <ModalTitle>
          {onBack && <ModalBackButton onBack={onBack} />}
          <Heading>{title}</Heading>
        </ModalTitle>
      </ModalHeader>
    )}
    <ModalBody>{children}</ModalBody>
  </ModalContainer>
)

export const SubModal = ({ children }) => (
  <SubModalContainer>
    <ModalBody justifyContent="space-between">{children}</ModalBody>
  </SubModalContainer>
)

export default Modal
