import React from 'react'
import TimerBlock from './TimerBlock'
import { Wrapper } from './styles'
import { OpenAtTimestamp } from '../../config/constant'

const Countdown = () => {
  const numberSeconds = OpenAtTimestamp - Math.floor(Date.now() / 1000)

  return (
    <Wrapper>
      <TimerBlock openAtSeconds={numberSeconds} />
    </Wrapper>
  )
}

export default Countdown
