import React from 'react'
import styled from 'styled-components'
import { useLocation } from 'react-router-dom'
import Box from 'components/Box'
import { urlConfig } from 'config/constant'
import MenuItem from './MenuItem'
import UserBlock from './Userblock'

export const StyledBurgerIcon = styled.button`
  left: 2rem;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  opacity: 0.7;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
  transition: opacity 400ms ease;

  @media (max-width: 1000px) {
    display: flex;
    padding-right: 40px;
  }

  &:focus {
    outline: none;
  }

  &:hover {
    opacity: 1;
  }

  div {
    width: 2rem;
    height: 0.25rem;
    background: #e0e0e0;
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;

    :first-child {
      transform: ${({ open }) => (open ? 'rotate(45deg)' : 'rotate(0)')};
    }

    :nth-child(2) {
      opacity: ${({ open }) => (open ? '0' : '1')};
      transform: ${({ open }) => (open ? 'translateX(20px)' : 'translateX(0)')};
    }

    :nth-child(3) {
      transform: ${({ open }) => (open ? 'rotate(-45deg)' : 'rotate(0)')};
    }
  }
`
const Navigation = styled.nav`
  position: absolute;
  top: 100px;
  left: 0;
  z-index: 10000;
  width: 100%;
  background-color: #1b1522;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: ${({ open }) => (open ? 'translateY(0px)' : 'translateY(-1000px)')};
  transition: transform 0.3s ease-in-out;
  @media (max-width: 1000px) {
    display: flex;
  }
`
const BurgerMenu = ({ onNavigateTo, open }) => {
  const { pathname } = useLocation()

  return (
    <Navigation open={open}>
      {urlConfig.map(({ name, path }) => {
        const isActive = path === '/' ? pathname === path : pathname.includes(path)
        return <MenuItem isActive={isActive} name={name} path={path} onNavigateTo={onNavigateTo} />
      })}
      <UserBlock />
    </Navigation>
  )
}

export default BurgerMenu
