import React from 'react'
import styled from 'styled-components'
import { useHistory } from 'react-router-dom'
import { SwitchTransition, CSSTransition } from 'react-transition-group'
import Flex from 'components/Flex'
import Text from 'components/Text'

const CardText = styled(Text)`
  color: #f3f3f3;
  line-height: 21.66px;
  letter-spacing: 3px;
  font-weight: 500;
`

const StyledActionButton = styled.button`
  background-image: url('/images/pack-opening-button.png');
  background-position: 0px 0px;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
  background-position: center;
  background-repeat: no-repeat;
  width: 45%;
  padding: 15px 0px;
  transition: opacity 100ms ease-in, transform 100ms ease-in;
  -webkit-font-smoothing: antialiased;
  background-color: transparent;
  border: none;
  &:hover {
    cursor: pointer;
  }
  &:active {
    transform: translateY(-5px);
    opacity: 0.6;
  }
`

const ActionButton = ({ onClick, step }) => {
  const history = useHistory()

  return (
    <SwitchTransition>
      <CSSTransition
        key={step}
        addEndListener={(node, done) => node.addEventListener('transitionend', done, false)}
        classNames="fadeslow"
      >
        <Flex width="100%" justifyContent={step === 1 ? 'center' : 'space-between'}>
          {step === 1 ? (
            <StyledActionButton onClick={onClick}>
              <CardText fontSize="2.2rem" mb="5px">
                Reveal
              </CardText>
            </StyledActionButton>
          ) : (
            <>
              <StyledActionButton onClick={onClick}>
                <CardText fontSize="2rem">View Card Detail</CardText>
              </StyledActionButton>
              <StyledActionButton onClick={() => history.push('/stash')}>
                <CardText fontSize="2rem">Back To Stash</CardText>
              </StyledActionButton>
            </>
          )}
        </Flex>
      </CSSTransition>
    </SwitchTransition>
  )
}

export default ActionButton
