import React from 'react'

import useCountdown from './hooks/useGetCountDownInSeconds'
import { BackgroundImage, Time, TimeValue, TimeLable, BallTop, BallBottom, BallLeft } from './styles'

const TimerBlock = ({ openAtSeconds }) => {
  const countTime = useCountdown(openAtSeconds)
  return (
    <BackgroundImage>
      <Time>
        <TimeValue>{countTime.day > 9 ? countTime.day : `0${countTime.day}`}</TimeValue>
        <TimeLable>Days</TimeLable>
      </Time>
      <Time>
        <TimeValue>{countTime.hours > 9 ? countTime.hours : `0${countTime.hours}`}</TimeValue>
        <TimeLable>Hours</TimeLable>
      </Time>
      <Time>
        <TimeValue>{countTime.minutes > 9 ? countTime.minutes : `0${countTime.minutes}`}</TimeValue>
        <TimeLable>Minutes</TimeLable>
      </Time>
      <Time>
        <TimeValue>{countTime.seconds > 9 ? countTime.seconds : `0${countTime.seconds}`}</TimeValue>
        <TimeLable>Seconds</TimeLable>
      </Time>
      <BallTop src="/images/common.png" />
      <BallLeft src="/images/rare.png" />
      <BallBottom src="/images/epic.png" />
    </BackgroundImage>
  )
}

export default TimerBlock
