import React, { useCallback, useMemo, useState } from 'react'
import styled from 'styled-components'
import { useHistory, useLocation } from 'react-router-dom'
import { useWeb3React } from '@web3-react/core'
import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'
import { urlConfig } from 'config/constant'
import useEthBalance from 'hooks/useEthBalance'
import MenuItem from './MenuItem'
import UserBlock from './Userblock'
import BurgerMenu, { StyledBurgerIcon } from './BurgerMenu'

const StyledWrapper = styled(Box)`
  background-color: transparent;
  background-image: linear-gradient(
    180deg,
    rgba(28, 22, 34, 0.98) 46%,
    rgba(31, 25, 36, 0.54) 84%,
    rgba(35, 30, 39, 0)
  );
`
const Row = styled.nav`
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  position: relative;
`

const StyledText = styled(Text)`
  font-size: 1.7rem;
  color: #b89ef4;
  cursor: pointer;
  opacity: 0.7;
  font-weight: 700;
  padding: 20px;
  transition: opacity 400ms ease;

  &:hover {
    opacity: 1;
  }
`

const LogoWrapper = styled.img`
  width: 150px;

  &:hover {
    cursor: pointer;
  }

  @media (max-width: 1000px) {
    padding: 10px 0px 10px 0px;
  }
`
const MenuItemWrapper = styled(Flex)`
  width: 100%;

  @media (max-width: 1000px) {
    display: none;
  }
`

const Header = () => {
  const history = useHistory()
  const { account } = useWeb3React()
  const [open, setOpen] = useState(false)
  const { pathname } = useLocation()
  const balance = useEthBalance()
  const onNavigateTo = useCallback((route) => {
    if (route) {
      history.push(route)
      setOpen(false)
    }
  }, [])

  const URL_CONFIG = useMemo(() => {
    if (account) {
      return urlConfig
    }

    return urlConfig.filter((link) => link.name !== 'Stash')
  }, [account])

  return (
    <StyledWrapper>
      <Row>
        <LogoWrapper src="/images/logo.png" onClick={() => onNavigateTo('/')} />

        <MenuItemWrapper justifyContent="flex-end" alignItems="center">
          {URL_CONFIG.map(({ name, path }) => {
            const isActive = path === '/' ? pathname === path : pathname.includes(path)
            return <MenuItem isActive={isActive} name={name} path={path} onNavigateTo={onNavigateTo} />
          })}
          {/* <StyledText>Genesis</StyledText> */}
          {/* <UserBlock balance={balance} /> */}
        </MenuItemWrapper>
        {/* <StyledBurgerIcon open={open} onClick={() => setOpen(!open)}>
          <Box />
          <Box />
          <Box />
        </StyledBurgerIcon> */}
        {/* <BurgerMenu onNavigateTo={onNavigateTo} open={open} /> */}
      </Row>
    </StyledWrapper>
  )
}

export default Header
