/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'

export const initialState = {
  selectedPackage: 'common',
  numberOfPackage: 1,
  isLoading: false,
}

export const purchaseSlice = createSlice({
  name: 'purchase',
  initialState,
  reducers: {
    setSelectedPackage: (state, action) => {
      state.selectedPackage = action.payload
    },
    changeNumberOfPackage: (state, action) => {
      state.numberOfPackage = action.payload
    },
    setLoading: (state, action) => {
      state.isLoading = action.payload
    },
  },
})

export const { reducer } = purchaseSlice
export const { setSelectedPackage, changeNumberOfPackage, setLoading } = purchaseSlice.actions
