import propTypes from '@styled-system/prop-types'

const Box = styled.div`
  box-sizing: border-box;
  ${color}
  ${space}
  ${border}
  ${grid}
  ${layout}
  ${typography}
`

Box.propTypes = {
  ...propTypes.space,
  ...propTypes.color,
  ...propTypes.border,
  ...propTypes.grid,
  ...propTypes.layout,
  ...propTypes.typography,
}

export default Box
