import styled from 'styled-components'
import { space, typography, layout, color } from 'styled-system'

const Text = styled.div`
  font-weight: ${({ bold }) => (bold ? 600 : 400)};
  ${({ textTransform }) => textTransform && `text-transform: ${textTransform};`}
  ${space}
  ${color}
  ${typography}
  ${layout}
`

Text.defaultProps = {
  small: false,
}

export default Text
