import React, { createContext, useState } from 'react'
import styled from 'styled-components'
import { CSSTransition } from 'react-transition-group'

const ModalWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
`

const Overlay = styled.div.attrs({ role: 'presentation' })`
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #110a19;
  transition: opacity 0.4s;
  opacity: ${({ show }) => (show ? 0.5 : 0)};
  z-index: ${({ zIndex }) => zIndex};
  pointer-events: ${({ show }) => (show ? 'initial' : 'none')};
`
export const Context = createContext({
  onPresent: () => {},
  onDismiss: () => {},
})

const ModalProvider = ({ children }) => {
  const [isOpen, setIsOpen] = useState(false)
  const [modalElement, setModalElement] = useState()

  const handlePresent = (node) => {
    setModalElement(node)
    setIsOpen(true)
  }

  const handleClose = () => {
    setModalElement(undefined)
    setIsOpen(false)
  }

  return (
    <Context.Provider
      value={{
        onPresent: handlePresent,
        onDismiss: handleClose,
        setCloseOnClickOverlay: () => {},
      }}
    >
      <CSSTransition in={isOpen} timeout={300} unmountOnExit classNames="fade">
        <ModalWrapper>
          <Overlay show onClick={handleClose} />
          {React.isValidElement(modalElement) &&
            React.cloneElement(modalElement, {
              onDismiss: handleClose,
            })}
        </ModalWrapper>
      </CSSTransition>
      {children}
    </Context.Provider>
  )
}

export default ModalProvider
