import React, { useState, useMemo } from 'react'
import styled from 'styled-components'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import LoaderIcon from 'components/LoaderIcon'
import { toast } from 'react-toastify'
import useOpenPackage from 'hooks/useOpenPackage'
import contractConfig from 'config/constant/contract'
import Modal from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
`
const WalletButton = styled.button`
  justify-content: center;
  display: flex;
  background-color: transparent;
  border: none;
  flex-direction: column;
  align-items: center;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:disabled {
    opacity: 0.2;
  }

  &:hover:disabled {
    cursor: not-allowed;
  }

  &:hover:enabled {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active:enabled {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const StyledSubText = styled(Text)`
  font-size: 1.1rem;
  color: #ffffff;
  line-height: 20px;
  letter-spacing: 2px;
`
const StyledHeaderText = styled(Text)`
  color: #f3f3f3;
  font-size: 2rem;
  margin-bottom: 35px;
  padding: 0 60px;

  @media (max-width: 576px) {
    padding: 0px;
  }
`

const OpenPackModal = ({ packageData, onNavigateToOpen, onDismiss }) => {
  const { commonPackageData, rarePackageData, epicPackageData } = packageData
  const {
    cyblockPresale: { commonPackageContractAddress, rarePackageContractAddress, epicPackageContractAddress },
  } = contractConfig
  const [isLoadingCommon, setIsLoadingCommon] = useState(false)
  const [isLoadingRare, setIsLoadingRare] = useState(false)
  const [isLoadingEpic, setIsLoadingEpic] = useState(false)
  const { onUnpack: onUnPackCommonPackage } = useOpenPackage(commonPackageContractAddress)
  const { onUnpack: onUnpackRarePackage } = useOpenPackage(rarePackageContractAddress)
  const { onUnpack: onUnpackEpicPackage } = useOpenPackage(epicPackageContractAddress)

  const onHandleOpenPackage = async (packageType) => {
    try {
      if (packageType === 'common') {
        setIsLoadingCommon(true)
        const packages = commonPackageData.map((index) => {
          return [
            Number(index),
            [
              [1, 2, 2, 1, '120398102938019238098'],
              [1, 0, 1, 1, '838293481723'],
              [1, 1, 0, 1, '584930802374'],
            ],
          ]
        })

        const proofs = commonPackageData.map(() => {
          return [
            1,
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
          ]
        })

        const result = await onUnPackCommonPackage(packages, proofs)
        onDismiss()
        setIsLoadingCommon(false)
      } else if (packageType === 'rare') {
        setIsLoadingRare(true)
        const packages = rarePackageData.map((index) => {
          return [
            Number(index),
            [
              [1, 2, 2, 1, '120398102938019238098'],
              [1, 0, 1, 1, '838293481723'],
              [1, 1, 0, 1, '584930802374'],
            ],
          ]
        })

        const proofs = rarePackageData.map(() => {
          return [
            1,
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
          ]
        })

        const result = await onUnpackRarePackage(packages, proofs)
        onDismiss()
        setIsLoadingRare(false)
      } else {
        setIsLoadingEpic(true)
        const packages = epicPackageData.map((index) => {
          return [
            Number(index),
            [
              [1, 2, 2, 1, '120398102938019238098'],
              [1, 0, 1, 1, '838293481723'],
              [1, 1, 0, 1, '584930802374'],
            ],
          ]
        })

        const proofs = epicPackageData.map(() => {
          return [
            1,
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
            '0x63616e6469646174653100000000000000000000000000000000000000000000',
          ]
        })

        const result = await onUnpackEpicPackage(packages, proofs)
        onDismiss()
        setIsLoadingEpic(false)
      }
    } catch (error) {
      console.log(error, 'error ?')
      toast.error('Fail to open package', {
        hideProgressBar: true,
      })
      setIsLoadingCommon(false)
    }
  }

  return (
    <Modal title="Your wallet">
      <Wrapper>
        <Box>
          <StyledHeaderText bold>Choose a pack type you want to open</StyledHeaderText>
        </Box>
        <Box mb="15px">
          <WalletButton
            disabled={commonPackageData.length === 0}
            onClick={() => {
              onHandleOpenPackage('common')
            }}
          >
            {isLoadingCommon ? (
              <LoaderIcon />
            ) : (
              <>
                <Text mr="5px" fontSize="1.6rem">
                  Common packs
                </Text>
                <StyledSubText mr="5px">{commonPackageData.length} available pack</StyledSubText>
              </>
            )}
          </WalletButton>
          <WalletButton
            disabled={rarePackageData.length === 0}
            onClick={() => {
              onHandleOpenPackage('rare')
            }}
          >
            {isLoadingRare ? (
              <LoaderIcon />
            ) : (
              <>
                <Text mr="5px" fontSize="1.6rem">
                  Rare packs
                </Text>
                <StyledSubText mr="5px">{rarePackageData.length} available pack</StyledSubText>
              </>
            )}
          </WalletButton>
          <WalletButton
            disabled={epicPackageData.length === 0}
            onClick={() => {
              onHandleOpenPackage('epic')
            }}
          >
            {isLoadingEpic ? (
              <LoaderIcon />
            ) : (
              <>
                <Text mr="5px" fontSize="1.6rem">
                  Epic packs
                </Text>
                <StyledSubText mr="5px">{epicPackageData.length} available pack</StyledSubText>
              </>
            )}
          </WalletButton>
        </Box>
      </Wrapper>
    </Modal>
  )
}

export default OpenPackModal
