import React from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import useAuth from 'hooks/useAuth'
import { SubModal } from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  padding: 20px 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
`
const StyledText = styled(Text)`
  font-size: 2rem;
  color: #fefefe;
  line-height: 17.15px;
  letter-spacing: 2px;
  margin-bottom: 15px;
  font-weight: 700;
`

const WalletButton = styled(Flex)`
  justify-content: center;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const LogoutModal = ({ onDismiss }) => {
  const formatAddress = (address) => {
    if (address) {
      const addressArr = address.split('')
      return `${addressArr.slice(0, 7).join('')}...${addressArr.slice(-7).join('')}`
    }

    return null
  }

  const { logout } = useAuth()
  const { account } = useWeb3React()
  return (
    <SubModal title="Your wallet">
      <Wrapper>
        <StyledText>{formatAddress(account)}</StyledText>
        <StyledText>Wallet connected</StyledText>
        <WalletButton
          onClick={() => {
            logout()
            onDismiss()
          }}
        >
          <Text>Log out</Text>
        </WalletButton>
      </Wrapper>
    </SubModal>
  )
}

export default LogoutModal
